@import "../variables";

.sort {
  position: relative;
  &__label {
    display: flex;
    align-items: center;
    font-size: 24px;
    @media (max-width: 389px) {
      font-size: 20px;
    }
    @media (max-width: 336px) {
      font-size: 16px;
    }
    svg {
      margin-right: 8px;
    }

    p {
      margin-right: 8px;
      color: #ffffff;
    }

    span {
      color: $gray;
      border-bottom: 1px dashed $gray;
      cursor: pointer;
    }
  }

  &__popup {
    position: absolute;
    right: 0;
    margin-top: 15px;
    background: #fff;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.09);
    border-radius: 10px;
    overflow: hidden;
    padding: 10px 0;
    width: 160px;

    ul {
      overflow: hidden;
      li {
        padding: 12px 20px;
        cursor: pointer;

        &.active,
        &:hover {
          background: rgba(148, 221, 255, 0.05);
        }

        &.active {
          font-weight: bold;
          color: #8f90f1;
        }
      }
    }
  }
}
