@import "../variables";

.categories {
  ul {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    @media (max-width: 447px) {
      display: block;
    }
    li {
      background-color: #f9f9f9;
      padding: 13px 20px;
      border-radius: 30px;
      margin-right: 10px;
      font-weight: bold;
      cursor: pointer;
      transition: background-color 0.1s ease-in-out;
      @include noselect();
      @media (max-width: 447px) {
        margin-right: 0;
        &:not(:last-child) {
          margin-bottom: 5px;
        }
      }
      &:hover {
        background-color: darken(#f9f9f9, 2%);
      }

      &:active {
        background-color: darken(#f9f9f9, 5%);
      }

      &.active {
        background-color: #282828;
        color: #fff;
      }
    }
  }
}
